.booking-page {
  overflow-x: hidden;
  overflow-y: hidden;
  min-height: 150vh;
  box-sizing: border-box;
  background: linear-gradient(#05dded80, transparent), url('../../images/new-york-page.png');
    /* url('../../images/airport.jpg'); */
  background-size: cover;
  background-position: center;
  width: 100%;
  margin: auto;
  margin-top: 0px;
  padding: 6rem;
}
/* header{
  margin-bottom: -60px;
} */

.body1 {
  margin-top: -200px;
  background: #ced4da !important;
  width: cover;

  object-position: center;
  display: inline-flex;

  align-self: center;
  align-content: center;
  justify-content: center;
}
.booking-page h1 {
  font-family: 'Pattaya', sans-serif;
  font-size: 10rem;
  padding: 4rem;
}

.booking-page body {
  background: transparent;
}
/* ------booking form----- */

.flight:hover {
  background-color: #fff !important;
  color: black;
  transition: 0.5s;
}
.booking-page .cloud,
.booking-page .cloud1 {
  overflow-x: hidden;
  height: 45rem;
  position: absolute;
  top: 0%;
  right: 0%;
  pointer-events: none;
}
.booking-page .cloud3 {
  height: 45rem;
  left: -10%;
  position: absolute;
  top: 10%;
  z-index: -1;
  animation: cloudAnimation 4s infinite alternate-reverse;
}
.plane1 {
  height: 100px;
  right: 0;
  margin-right: 500px;
  animation: planeAnimation 7s ease-in infinite;
}
.moving-cloud-2 {
  top: 1%;
}
@keyframes cloudAnimation {
  0% {
    transform: translate(5%, -15%) rotateX(360deg) rotateY(180deg);
  }

  100% {
    transform: translate(0%, 0%) rotateX(360deg) rotateY(180deg);
  }
}
@keyframes planeAnimation {
  0% {
    transform: scale(0.5) rotate(0deg) translate(-300px, 400px);

    /*  */
  }

  50% {
    transform: scale(1) rotate(30deg) translate(0px, 200px);
  }

  100% {
    transform: scale(2.8) rotate(20deg) translate(300px, -400px);
  }
}

/* media query */
@media screen and (max-width: 952px) {
  .booking-form-box {
    width: 100% !important;
    margin: auto;
    padding: 0rem;
    box-sizing: border-box;
    /* background: #ced !important; */
    border: black solid 1rem;
  }
  .radio-btn {
    margin-top: -10rem;
  }
  .body1 {
    width: 100%;
    box-sizing: border-box;
    object-position: center;
    display: flex;
    margin: auto;
    position: flex;
    align-self: center;
    align-content: center;
    justify-content: center;
  }
  .booking-page body {
    width: 100%;
    height: 600px;
    margin: auto;
    margin-top: 12rem;
    position: absolute;
    top: 10%;
  }
  .search-form {
    background: #ced;
  }
  .booking-page {
    border: 10px solid #131c27;
    display: inline-block;
    /* position: absolute; */
    width: 100%;
    position: relative;
  }
}
@media screen and (max-width: 452px) {
  .booking-form-box {
    width: 100%;
    margin: auto;
    padding: 2rem;
    box-sizing: border-box;
  }
  .booking-page h1 {
    font-size: 7rem;
    display: inline-block;
    margin-top: 10rem;
    margin-bottom: 160rem;
  }
  .booking-form-box h1 {
    margin: 2rem;
    margin-bottom: -100px;
    font-size: 40px;
  }
  .body1 {
    width: 100%;
    box-sizing: border-box;
    object-position: center;
    display: flex;
    margin: auto;
  }
  .booking-page body {
    width: 100%;
    height: 600px;
    margin: auto;
    margin-top: 22rem;
    position: absolute;
    top: 10%;
  }
  .booking-form-box {
    height: 500px;
    margin-top: -10rem;
    background: #131c27;
  }
}

/* Search Form */
.booking-page * {
  box-sizing: border-box;
}

.booking-page #search-form {
  border: 1px solid #131c27;
  width: 900px;
  position: relative;
}

.booking-page #header {
  height: 80px;
  background: #131c27;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.booking-page #header h1 {
  color: #fff;
  margin-left: 20px;
  font-size: 1.3em;
}

.booking-page .flight {
  padding: 20px;
}
.flight input,
select {
  color: #6b6a6a;
}

.info-box {
  padding: 10px 0;
  /* margin-top: 20px; */
  flex: 1;
  border-right: 20px solid transparent;
}
.info-box:last-child {
  border-right: none;
}
.info-box label {
  display: block;
  color: black;
  padding-bottom: 1rem;
  font-size: 0.9em;
}
.info-box input[type='text'],
input[type='submit'],
select {
  border: none;
  outline: none;
  background: #e2e0e0;
  margin-top: 5px;
  width: 100%;
  padding: 5px;
  height: 30px;
  border-radius: 3px;
}

#flight-type {
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px dotted silver;
}
#flight-type .info-box {
  flex: 0 1 25%;
  display: flex;
  align-items: center;
}

#flight-depart {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px dotted silver;
}
#flight-depart .info-box {
  position: relative;
}
#depart-res,
#arrive-res {
  position: absolute;
  background: #ffffff;
}
#depart-res p,
#arrive-res p {
  padding: 7px 2px;
  border-bottom: 1px dotted silver;
}
#depart-res p:hover,
#arrive-res p:hover {
  background: #ededed;
  cursor: pointer;
}

#flight-dates {
  display: flex;
  border-bottom: 1px dotted silver;
}

#flight-info {
  display: flex;
  border-bottom: 1px dotted silver;
}

#flight-search .info-box {
  display: flex;
  justify-content: center;
}
#flight-search input[type='submit'] {
  background: #55af3a;
  color: white;
  padding: 0;
  height: 50px;
  font-size: 1.2em;
}

/* Calender */
#calender {
  border: 3px solid red;
  padding: 5px;
  opacity: 0;
  background: #ffffff;
  position: absolute;
  transition: opacity 0.3s;
  pointer-events: none;
}
#calender .nav {
  padding: 3px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}
#calender .nav button {
  background: #ff3240;
  border: none;
  outline: none;
  color: #ffffff;
  padding: 5px 8px;
  font-weight: 600;
  border-radius: 3px;
}
.booking-page table {
  border: 1px solid #dbdbdb;
  background: #f4f4f4;
}
table td {
  border: 1px solid #dbdbdb;
  padding: 5px;
  text-align: center;
  cursor: pointer;
}
.normal {
  color: rgba(0, 0, 0, 0.8);
}
.today {
  background: #ff3240;
  color: #ffffff;
}
.closed {
  color: #dbdbdb;
  cursor: default;
}
.picked {
  background: green;
}
#month {
  display: inline-block;
}

#confirm {
  width: 500px;
  padding: 20px;
  border: 5px solid #55af3a;
  margin: 20px auto;
  opacity: 0;
}
#confirm h3 {
  font-size: 1.2em;
  margin-bottom: 20px;
}
#confirm p {
  padding: 10px;
}
#confirm strong {
  font-weight: 600;
}
