/* GLOBAL STYLES? */
* {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
* h1,
* header,
* h5,
* h2,
* h3,
* button,
* .select,
* a,
* img, * div{
  overflow-y: hidden;
}

html {
  font-size: 62.5%;
}

h1 {
  font-size: 4.6rem;
}
li,
button,
label,
input,
p {
  font-size: 2rem;
}
h2 {
  font-size: 4.8rem;
}
h3 {
  font-size: 3rem;
  font-weight: 400;
}
h4,
h5 {
  font-size: 2.8rem;
}
a {
  color: white;
  text-decoration: none;
}
button {
  padding: 2rem 6rem;
  background-color: #4c6e97;
  border: none;
  color: white;
  font-size: 1.8rem;
  cursor: pointer;
  /* transition: all 1s ease-in-out; */
  /* animation: cloudAnimations 2s infinite; */
}
button:hover {
  background-color: #334459;
  font-size: 2rem;
}
a:hover {
  text-decoration: none;
  color: white;
}
.modal {
  width: 100%;
  margin: 0 auto;
  position: absolute;
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
  min-height: 1200px;
  background: url(/static/media/airplaneBackground.dd341e65.jpg);
  background-size: cover;
  background-position: center;
  flex-wrap: wrap;
  z-index: 0;
}
.modal header {
  margin-top: 15rem;
  margin-bottom: 4rem;
}
.modal h1 {
  text-shadow: 1px 1px 2px red, 0 0.1em brown, 0 0 7.2em rgb(251, 255, 7);
  font-size: 10rem;
  margin-top: 3rem;
}
.modal a {
  text-decoration: underline !important;
  color: black;
}
.modal form {
  flex-wrap: wrap;
  right: 30%;
  z-index: 0;
  margin: 0 auto;
  margin-top: -3rem;
  /* border: brown solid 5px; */
  /* background: url('./images/travlBackground2.png'),orange; */
  background: linear-gradient(rgba(42, 60, 78, 0), transparent),
    rgba(73, 90, 185, 0.5);
  background-position: center;
  background-size: cover;
  min-height: 420px;
  max-width: 620px;
}
.modal input {
  border: black solid 4px;
  margin: 0 auto;
  flex-wrap: wrap;
  padding-top: 2rem;
  margin-top: 4rem;
  display: flex;
  min-width: 300px;
}
.modal label {
  display: flex;
  text-align: center;
  justify-content: center;
}
.modal button {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.palmLeft {
  position: absolute;
  -webkit-animation: palmAnimation alternate 3s infinite;
          animation: palmAnimation alternate 3s infinite;

  max-height: 400px;
  left: 0%;
  top: 40%;
}
.palmRight {
  position: absolute;
  animation: palmAnimation alternate-reverse 3s infinite;
  transform: perspective(500px) translate(10px, 0, 20px) rotateY(3deg);
  max-height: 400px;
  right: 0%;
  top: -3%;
  z-index: -1;
}

body {
  font-family: 'Poppins', sans-serif;
}
/* navigation section with hero */
.main-head {
  background-color: #131c27;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  z-index: 1;
}
nav {
  min-height: 10vh;
  display: flex;
  width: 90%;
  /* padding: 2rem; */
  /* vertical? */
  margin-top: 600px;
  margin: auto;
  flex-wrap: wrap; /*for mobile resposiveness on the nav buttons*/
}
nav ul {
  display: flex;
  flex: 4 15 40rem; /*40 rem allows for quicker switch in screen size*/
  justify-content: space-around;
  align-items: flex-end;
  list-style: none;
}
#logo {
  font-family: 'Pattaya', sans-serif;
  font-weight: 400;
  display: flex;
  flex: 5 4 0rem;
  justify-content: flex-start; /* used to align horizontally */
  align-items: center; /* used to align vertically */
}
.hero {
  min-height: 90vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), transparent),
    url(/static/media/landing-page.ac3532d2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center; /* vertically  */
  align-items: center; /* horizontally */
  text-align: center;
}
.hero h3 {
  padding: 5rem;
}
/* locations section */
#locations {
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), transparent),
    url(/static/media/new-york-page.fa697f32.png);
  background-size: cover;
  background-position: center;
  /* display: flex; */
  align-items: center;
  position: relative;
  left: 0px;
  overflow: hidden;
}
.locations-head {
  width: 90%;
  margin: auto;
  margin-top: 8rem;
}
.locations-head h2,
.locations-head h3 {
  display: flex;
  justify-content: start;
  text-decoration: underline;
  padding: 1rem;
}

.locations-head button {
  display: flex;
  justify-content: start;
  text-decoration: none;
  margin-top: 20px;
}
.globe {
  height: 250px;
  -webkit-animation: globeAnimation 15s infinite linear;
          animation: globeAnimation 15s infinite linear;
}
@-webkit-keyframes globeAnimation {
  0% {
    transform: rotateY(0deg) translateX(0%);
  }
  100% {
    transform: rotateY(360deg);
  }
}
@keyframes globeAnimation {
  0% {
    transform: rotateY(0deg) translateX(0%);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.cloud {
  height: 45rem;
  position: absolute;
  top: 0%;
  right: 0%;
  pointer-events: none;
}
.moving-cloud-1 {
  animation: cloudAnimations 4s infinite alternate-reverse;
}
.moving-cloud-2 {
  animation: cloudAnimations 9s infinite alternate-reverse;

  top: 10%;
  z-index: -1;
  opacity: 0.9;
}

/* BENEFITS SECTION */

.benefits-head {
  background: #343c44;
  padding: 3rem;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
