  border-bottom-right-radius: 20px;
}
.form-wrapper input {
  padding: 1rem 3rem;
}
footer {
  color: white;
  background: rgba(19, 28, 39, 1);
}
.footer-wrapper {
  display: flex;
  padding: 3rem;
  width: 90%;
  margin: auto;
  min-height: 10vh;
  align-items: center;
  flex-wrap: wrap;
}
footer h5 {
  flex: 1 1 40rem;
}
footer ul {
  display: flex;
  flex: 10 1 40rem;
  list-style: none;
  justify-content: space-between;
  align-items: center;
}
@-webkit-keyframes palmAnimation {
  0% {
    transform: translate(0%, 0%);
  }

  100% {
    transform: translate(0%, 5%);
  }
}
@keyframes palmAnimation {
  0% {
    transform: translate(0%, 0%);
  }

  100% {
    transform: translate(0%, 5%);
  }
}
@-webkit-keyframes buttonAnimations {
  0% {
    background: lightblue;
  }
  25% {
    background: lightsalmon;
  }
  100% {
    background: lime;
  }
}
@keyframes buttonAnimations {
  0% {
    background: lightblue;
  }
  25% {
    background: lightsalmon;
  }
  100% {
    background: lime;
  }
}
@-webkit-keyframes cloudAnimations {
  0% {
    transform: translate(10%, -15%);
  }

  100% {
    transform: translate(0%, 0%);
  }
}
@keyframes cloudAnimations {
  0% {
    transform: translate(10%, -15%);
  }

  100% {
    transform: translate(0%, 0%);
  }
}
